	        desc: '公司率先在国内研发成功新一代高品质包布三角带（即第三代新品聚酯硬线绳三角带），填补国内空白，质量达到国际先进水平，荣获国家重点新产品、国家发明专利。以“四大核心”创新创造精品，新材料：高模低缩硬线绳骨架材料，优质橡胶及助剂新材料；新工艺：具有自主知识产权国家发明专利的全新独创的制造工艺；新配方：氯丁胶多胶种复合材料；新装备：具有国际一流水平的智能装备生产线。拥有卓越性能，国际先进水平：高强度、耐冲击、大功率超负荷不伸长、耐高温、耐油、免调整，特别适用重型机械传动，摩擦系数大，传递效率高，使用寿命长。高品质包布V带是在普通V带的基础上进行材料工艺上的革新：一是拉伸强度提高了10%以上，最大特点运转过程中不易伸长，而传统三角带易伸长，特别在C、D型高负荷工况下非常明显。二是A、B型参考力伸张率降到≤4%,普通V带的标准为≤8%。三是实际寿命比普通V带标准大大提高。四是其创新点在于由传统的软聚酯线绳改变为硬聚酯线绳，从而大大提高了线绳强度和减少收缩，同时研究浸胶布配方、线绳浸胶配方，粘合、伸张胶配方，底胶配方，并采用切割式工艺，属国内首创，填补了国内空白。本公司硬线绳三角带经过几年的推广与发展，深受用户的好评，产品供不应求，已初步形成替代聚酯软线绳老产品的趋势。'
