<template>

  <div v-show="show" class="popup-root">
    <mt-popup :visible.sync="show" class="mint-popup" position="top">
      <a href="javascript:;" @click="toggleBar()" title="关闭" hidefocus="true" class="close"></a>
      <div class="content-box">
        <img :src="item.image" alt="">
        <div class="info">
          <span class="title">{{item.title}}</span>
          <!--v-html="item.desc"-->
          <span class="desc" v-if="item.desc != '水稻收割机皮带'" v-html="item.desc"></span>
        </div>
      </div>
    </mt-popup>
  </div>
  <!--<div v-show="show" class="sidebar-mask" @click="toggleBar"></div>-->

</template>
<style scoped>

  .popup-root {
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    display: flex;
    justify-content: center;
    z-index: 1000;
  }

  .mint-popup {
    width: 60%;
    align-self: center;
    height: 70%;
    background: #ffffff;
  }

  .title {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 16px;
  }

  .content-box {
    overflow-y: scroll;
    overflow-x: hidden;
    height: 90%;
    display: flex;
    margin-top: 44px;
    padding: 4px 4px 4px 32px;
  }

  .content-box > img {
    width: 300px;
    height: 300px;
  }

  .info {
    padding-right: 16px;
    display: flex;
    flex-direction: column;
    overflow: scroll;
    margin-left: 16px;
  }

  .close {
    display: block;
    width: 32px;
    height: 32px;
    background: url("../assets/img/close.png") no-repeat center;
    background-size: 24px;
    position: absolute;
    right: 16px;
    top: 8px;
  }

</style>
<script>
  import MtPopup from 'mint-ui/packages/popup/src/popup.vue';

  export default{
    props: ['show', 'item'],
    methods: {
      toggleBar(){
        this.show = !this.show;
      }
    },
    data(){
      return {}
    },
    components: {MtPopup}
  }
</script>



